.search-engine .hideList {
  display: none;
}

.search-engine ul {
  box-sizing: border-box;
  width: 100%;
  list-style: none;
  font-size: 1rem;
  color: #493636;
  padding: 0;
  padding-top: .5rem;
  padding-bottom: 1rem;
  background: rgba(70, 62, 40, 0.042);
  border-radius: 8px;
  font-weight: 500;
  max-width: 600px;
  backdrop-filter: blur(5px);
  -webkit-backdrop-filter: blur(2px);
  border: 1px solid rgba(70, 62, 40, 0.035);
  min-height: 0px;
}

.search-engine ul li {
  display: flex;
  flex-wrap: wrap;
  cursor: pointer;
  color: rgba(90, 90, 90, 0.733);
  background-color: rgba(255, 255, 255, 0);
  padding-top: 3%;
  padding-bottom: 3%;
  text-indent: 20px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.079);
}

.search-engine ul li:hover {
  background: rgba(70, 62, 40, 0.035);
  color: rgba(52, 52, 52, 0.8);
}

.search-engine .inputd {
  cursor: pointer;
  display: flex;
  flex-direction: row;
  height: 30px;
  align-items: center;
  justify-content: flex-end;
  width: 530px;
  transition: 500ms ease-in-out;
}

.search-engine .inputd img {
  height: 40px;
  padding: 20px;
  position: absolute;
  cursor: pointer;
}

.search-engine .inputd input {
  position: absolute;
  outline: none;
  box-sizing: border-box;
  width: 100%;
  font-size: 1.5rem;
  height: 60px;
  color: #493636;
  padding-left: 20px;
  padding-right: 60px;
  background: rgba(70, 62, 40, 0.111);
  border-radius: 8px;
  font-weight: 700;
  backdrop-filter: blur(5px);
  -webkit-backdrop-filter: blur(2px);
  border: 1px solid rgba(70, 62, 40, 0.035);
}

.search-engine .inputd input::placeholder {
  color: #493636;
  font-weight: 700;
}

.search-engine .small {
  cursor: pointer;
  display: flex;
  flex-direction: row;
  height: 30px;
  align-items: center;
  justify-content: flex-end;
  width: 10px;
}
